<template>
    <div class="query">
        <div class="queryHeader" v-if="name!='[角色昵称]'">
            <h3>{{name}}</h3>
            <span class="queryOther" @click="Reset">返回</span>
            <p>
                <span>{{infoArr[11]}}</span>
                <span>{{infoArr[16]}}</span>
                <span>{{infoArr[19]}}</span>
                <span>{{power}}</span>
                <span>{{infoArr[23]}}</span>
            </p>
        </div>

        <div class="queryLeft" v-if="mbData">
            <div class="LeftAttack">
                <div class="InfoHeader">
                    攻击：{{mbData.records.total_ability.attack_power_value}}
                </div>
                <div class="InfoItem">
                    <span>PVP攻击</span>
                    <span>{{mbData.records.total_ability.pc_attack_power_value}}</span>
                </div>
                <div class="InfoItem">
                    <span>降魔攻击</span>
                    <span>{{mbData.records.total_ability.boss_attack_power_value}}</span>
                </div>
                <div class="InfoItem">
                    <span>穿刺</span>
                    <span>{{mbData.records.total_ability.attack_pierce_value}}</span>
                </div>
                <div class="InfoItem">
                    <span>命中</span>
                    <span>{{mbData.records.total_ability.attack_hit_value}}</span>
                </div>
                <div class="InfoItem">
                    <span>集中</span>
                    <span>{{mbData.records.total_ability.attack_concentrate_value}}</span>
                </div>
                <div class="InfoItem">
                    <span>暴击</span>
                    <span>{{mbData.records.total_ability.attack_critical_value}}</span>
                </div>
                <div class="InfoItem">
                    <span>暴伤</span>
                    <span>{{mbData.records.total_ability.attack_critical_damage_value}}</span>
                </div>
                <div class="InfoItem">
                    <span>熟练</span>
                    <span>{{mbData.records.total_ability.attack_stiff_duration_level}}</span>
                </div>
                <div class="InfoItem">
                    <span>额外伤害</span>
                    <span>{{mbData.records.total_ability.int_attack_damage_modify_diff}}</span>
                </div>
                <div class="InfoItem">
                    <span>嘲讽</span>
                    <span>{{mbData.records.total_ability.hate_power_rate}}%</span>
                </div>
                <div class="InfoItem">
                    <span>异常伤害率</span>
                    <span>{{mbData.records.total_ability.abnormal_attack_power_value}}</span>
                </div>
                <div class="InfoItem">
                    <span>功力</span>
                    <span>{{mbData.records.total_ability.attack_attribute_value}}</span>
                </div>
            </div>
            <div class="RightDefense">
                <div class="InfoHeader">
                    血量：{{mbData.records.total_ability.max_hp}}
                </div>
                <div class="InfoItem">
                    <span>防御</span>
                    <span>{{mbData.records.total_ability.int_defend_power_value}}</span>
                </div>
                <div class="InfoItem">
                    <span>PVP防御</span>
                    <span>{{mbData.records.total_ability.pc_defend_power_value}}</span>
                </div>
                <div class="InfoItem">
                    <span>降魔防御</span>
                    <span>{{mbData.records.total_ability.boss_defend_power_value}}</span>
                </div>
                <div class="InfoItem">
                    <span>闪避</span>
                    <span>{{mbData.records.total_ability.defend_dodge_value}}</span>
                </div>
                <div class="InfoItem">
                    <span>格挡</span>
                    <span>{{mbData.records.total_ability.defend_parry_value}}</span>
                </div>
                <div class="InfoItem">
                    <span>暴防</span>
                    <span>{{mbData.records.total_ability.int_defend_critical_value}}</span>
                </div>
                <div class="InfoItem">
                    <span>韧性</span>
                    <span>{{mbData.records.total_ability.int_defend_stiff_duration_level}}阶段</span>
                </div>
                <div class="InfoItem">
                    <span>伤害减免</span>
                    <span>{{mbData.records.total_ability.int_defend_damage_modify_diff}}</span>
                </div>
                <div class="InfoItem">
                    <span>恢复</span>
                    <span>{{mbData.records.total_ability.int_hp_regen}}</span>
                </div>
                <div class="InfoItem">
                    <span>治疗</span>
                    <span>{{mbData.records.total_ability.heal_power_rate}}%</span>
                </div>
                <div class="InfoItem">
                    <span>异常防御率</span>
                    <span>{{mbData.records.total_ability.abnormal_defend_power_value}}</span>
                </div>
            </div>
        </div>

        <div class="queryRight" v-if="bgArr.length != 0">
            <div class="weapon">
                <span>{{wq.alt}}</span>
                <span>耐久：{{nj}} / 100</span>
            </div>

            <div class="zbItem" v-for="(item,index) in zbArr" :key="index">
                {{item.alt}}
            </div>
            <div class="zbFooter">
                <div class="bg">
                    <img v-for="(item,index) in bgArr" :key="index" :src="item" alt="index">
                </div>
                <div class="enchant">
                    <img v-for="(enchant, index) in enchantArr" :key="index" :src="enchant.src">
                </div>
            </div>

        </div>

        <div v-if="name=='[角色昵称]'" class="search">
            <input @keyup.enter="search" type="text" v-model="searchValue" placeholder="输入角色昵称查看装备" autofocus="autofocus" autocomplete="off">
            <svg @click="search" class="icon" style="width: 50px; height: 29px;vertical-align: middle;fill: currentColor;overflow: hidden;" viewBox="0 0 1024 1024" version="1.1" xmlns="http://www.w3.org/2000/svg" p-id="499">
                <path d="M471.68 742.4a256 256 0 0 1-254.4-226.88 32 32 0 0 1 64-7.04 189.12 189.12 0 0 0 55.04 113.6A194.24 194.24 0 0 0 480 678.08a32 32 0 0 1 2.88 64z" fill="#F99977" p-id="500"></path>
                <path d="M471.68 86.4a400 400 0 1 0 273.6 690.88l157.44 157.44a32 32 0 0 0 45.44 0 32 32 0 0 0 0-45.12l-160-160a399.68 399.68 0 0 0-316.48-643.2z m0 736a336.64 336.64 0 1 1 237.44-98.56 333.44 333.44 0 0 1-237.44 98.56z" fill="#000000" p-id="501"></path>
                <path d="M378.752 298.496m-79.744 0a79.744 79.744 0 1 0 159.488 0 79.744 79.744 0 1 0-159.488 0Z" fill="#99FF99" p-id="573"></path>
            </svg>
        </div>

    </div>
</template>

<script>
export default {
    data() {
        return {
            name: '[角色昵称]',
            power: '',
            wq: {},
            enchantArr: [],
            zbArr: [],
            bgArr: [],
            infoArr: [],
            mbData: null,
            nj: '',
            result: null,
            searchValue: ''
        }
    },

    created() {
        this.inter()
    },

    methods: {
        inter() {
            // this.name = '[角色昵称]';
            this.power = '';
            this.wq = {};
            this.enchantArr = [];
            this.zbArr = [];
            this.bgArr = [];
            this.infoArr = [];
            this.mbData = null;
            this.nj = '';
            this.result = null;
            this.searchValue = '';
            if (Object.keys(this.$route.query).length != 0) {
                this.name = Object.keys(this.$route.query)[0];
                this.getData('info');
                this.getData('Panel');
                this.getData('Equi')
            }
        },

        getData(type) {
            type == 'Panel' ? type = 'data/abilities.json' : type == 'Equi' ? type = 'data/equipments' : type = "profile";
            let getDataXhr = new XMLHttpRequest();
            getDataXhr.open('GET', `hug/ingame/bs/character/${type}?c=${this.name}&s=1919`)
            getDataXhr.send();
            getDataXhr.onreadystatechange = () => {
                if (getDataXhr.status == 200 && getDataXhr.readyState == 4) {
                    if (type == "profile") {
                        this.setInfo(getDataXhr.response.toString().replace(/\n/g, ''))
                    }
                    if (type == 'data/abilities.json') {
                        this.setPanel(JSON.parse(getDataXhr.response));
                    }
                    if (type == 'data/equipments') {
                        this.setEquip(getDataXhr.response.toString().replace(/\n/g, ''));
                    }
                }
            }
        },

        setInfo(data) {
            this.result = data.substring(data.indexOf('<span class="name">'), data.indexOf("<!-- haveLike_NP"));
            let info = this.matchReg(this.result);
            this.infoArr = info.split("|");

            if (this.infoArr.length <= 21) {
                return
            }
            this.name = this.infoArr[1].slice(1, this.infoArr[1].length - 1);
            this.power = this.infoArr[21].split('&nbsp;').join('')
        },

        setPanel(data) {
            this.mbData = data;
        },

        setEquip(data) {
            this.result = data.substring(data.indexOf("<!-- 무기 -->"), data.indexOf("<!-- 보석 -->"));
            this.nj = this.result.match(/class="text">?([^\'\"]*)\/ /)[1];

            let imgReg = /<img.*?(?:>|\/>)/g;
            let altReg = /alt=[\'\"]?([^\'\"]*)[\'\"]?/;
            let srcReg = /src=[\'\"]?([^\'\"]*)[\'\"]?/;

            let arr = this.result.match(imgReg);
            let src = '';
            let alt = '';

            if (arr) {
                this.wq.src = arr[0].match(srcReg)[1];
                this.wq.alt = arr[0].match(altReg)[1];
            }

            if (!arr) {
                return
            }

            for (let i = 1; i < arr.length; i++) {
                src = arr[i].match(srcReg)[1];
                alt = arr[i].match(altReg);
                alt == null ? alt = [null, null] : alt = alt;
                this.enchantArr.push({ src, alt: alt[1] })
            }

            this.result = data.substring(data.indexOf("<!-- 반지 -->"), data.indexOf("<!-- (e) 착용보패 -->"));

            // console.log(this.result);


            arr = this.result.match(imgReg);

            for (let i = 0; i < arr.length - 9; i++) {
                src = arr[i].match(srcReg)[1];
                alt = arr[i].match(altReg);
                alt == null ? alt = [null, null] : alt = alt;
                this.zbArr.push({ src, alt: alt[1] })
            }

            // console.log(arr);

            for (let j = arr.length - 9; j < arr.length - 1; j++) {
                src = arr[j].match(srcReg)[1];
                this.bgArr.push(src);
            }
        },

        matchReg(str) {
            let reg = /<\/?.+?\/?>/g;
            return str.replace(reg, '|').replace(/\s+/g, "")
        },

        search() {
            this.$router.replace(`/query?${this.searchValue}`)
        },

        Reset() {
            this.name = '[角色昵称]';
            this.$router.replace(`/query`)
        }

    },
    
    watch: {
        "$route.query": function () {
            this.inter()
        }
    },
}
</script>

<style>
.query {
    display: flex;
    flex-wrap: wrap;
}

.queryHeader {
    position: relative;
    position: sticky;
    top: 0;
    width: 100%;
    background: var(--pbg);
    color: var(--tc);
    opacity: 0.9;
}

.queryHeader h3 {
    overflow: hidden;
    width: calc(100% - 500px);
    text-overflow: ellipsis;
    white-space: nowrap;
    font-size: 22px;
}

.queryHeader .queryOther {
    position: absolute;
    top: 0;
    right: 10px;
    font-weight: 600;
    font-size: 14px;
    line-height: 25px;
    cursor: pointer;
}

.queryHeader .queryOther:hover {
    color: var(--hover);
}

.queryHeader p {
    margin: 10px auto;
    font-size: 16px;
}

.queryHeader p span + span {
    position: relative;
    display: inline-block;
    margin-left: 12px;
}

.queryHeader p span + span:before {
    position: absolute;
    top: 20%;
    left: -7px;
    width: 2px;
    height: 70%;
    background: #999;
    content: "";
    pointer-events: none;
}

.queryLeft,
.queryRight {
    width: 50%;
    height: 100%;
    font-size: 16px;
}

.queryLeft {
    display: flex;
    padding-right: 5px;
    flex-wrap: wrap;
}

.queryRight {
    padding-left: 10px;
}

.LeftAttack,
.RightDefense {
    padding-right: 5px;
    width: 50%;
}

.RightDefense {
    padding-left: 5px;
}

.LeftAttack .InfoHeader {
    background: var(--c4);
    color: var(--c4tc);
}

.InfoHeader {
    margin-bottom: 5px;
    padding: 50px 0;
    border-radius: 10px;
    text-align: center;
    font-weight: 600;
    font-size: 1.2em;
}

.RightDefense .InfoHeader {
    background: var(--c5);
    color: var(--c5tc);
}

.InfoItem {
    display: flex;
    margin: 3px auto;
    padding: 15px;
    border-radius: 5px;
    background: var(--c2);
    color: var(--c2tc);
    justify-content: space-between;
}

.RightDefense .InfoItem {
    background: var(--c3);
    color: var(--c3tc);
}

.InfoItem span {
    line-height: 100%;
}

.weapon {
    display: flex;
    margin-bottom: 10px;
    padding: 20px 15px;
    width: calc(100% - 5px);
    border-radius: 10px;
    font-weight: 600;
    font-size: 20px;
    justify-content: space-between;
}

.weapon,
.zbItem {
    background: var(--c1);
    color: var(--c1tc);
}

.zbItem {
    display: inline-block;
    overflow: hidden;
    margin-top: 5px;
    padding: 13px 0;
    border-radius: 5px;
    text-align: center;
    text-overflow: ellipsis;
    white-space: nowrap;
}

.zbItem:nth-child(2n) {
    margin-right: 5px;
    width: calc(50% - 5px);
}

.zbItem:nth-child(odd) {
    margin-left: 5px;
    width: calc(50% - 10px);
}

.zbFooter {
    display: flex;
    margin-top: 10px;
}

.bg {
    position: relative;
    display: inline-block;
    width: 128px;
    height: 128px;
}

.bg img {
    position: absolute;
}

.bg img:first-child {
    top: 0;
    left: 32px;
}

.bg img:nth-child(2) {
    top: 0;
    left: 64px;
}

.bg img:nth-child(3) {
    top: 32px;
    left: 64px;
}

.bg img:nth-child(4) {
    top: 64px;
    left: 64px;
}

.bg img:nth-child(5) {
    top: 64px;
    left: 32px;
}

.bg img:nth-child(6) {
    top: 64px;
    left: 0;
}

.bg img:nth-child(7) {
    top: 32px;
    left: 0;
}

.bg img:nth-child(8) {
    top: 0;
    left: 0;
}

.enchant {
    display: inline-block;
    width: calc(100% - 128px);
}

.search {
    width: 100%;
    margin-top: 20vh;
    text-align: center;
}

.search input {
    box-sizing: border-box;
    padding: 0 65px 0 10px;
    width: 50%;
    height: 40px;
    outline: 0;
    border: 2px solid #000;
    border-radius: 6px;
    text-align: left;
    font-weight: 600;
    font-size: 20px;
    line-height: 50px;
}

.search svg {
    width: 50px;
    height: 29px;
    border-left: 2px solid #000;
    cursor: pointer;
    margin-left: -60px;
    margin-top: -3px;
    z-index: 1000;
}

@media screen and (max-width: 900px) {
    .queryHeader h3 {
        width: calc(100% - 100px);
        font-size: 16px;
    }

    .queryHeader .queryOther {
        font-size: 12px;
    }

    .queryHeader p {
        margin: 10px auto;
        font-size: 14px;
    }

    .queryLeft,
    .queryRight {
        width: 100%;
        font-size: 14px;
    }

    .queryRight {
        margin-top: 10px;
        padding-left: 0;
    }

    .weapon {
        font-weight: 500;
        font-size: 16px;
    }

    .InfoHeader {
        padding: 30px 0;
    }

    .InfoItem {
        padding: 10px 15px;
    }

    .zbItem {
        padding: 10px 0;
    }

    .bg {
        width: 64px;
        height: 64px;
    }

    .bg img {
        width: 32px;
    }

    .bg img:first-child {
        top: 0;
        left: 16px;
    }

    .bg img:nth-child(2) {
        top: 0;
        left: 32px;
    }

    .bg img:nth-child(3) {
        top: 16px;
        left: 32px;
    }

    .bg img:nth-child(4) {
        top: 32px;
        left: 32px;
    }

    .bg img:nth-child(5) {
        top: 32px;
        left: 16px;
    }

    .bg img:nth-child(6) {
        top: 32px;
        left: 0;
    }

    .bg img:nth-child(7) {
        top: 16px;
        left: 0;
    }

    .bg img:nth-child(8) {
        top: 0;
        left: 0;
    }

    .enchant {
        margin-left: 20px;
        width: calc((100% - 64px) / 2);
    }

    .enchant img {
        width: 32px;
    }

    .search input {
        box-sizing: border-box;
        padding: 0 65px 0 10px;
        width: 80%;
        height: 35px;
        outline: 0;
        border: 1px solid #000;
        border-radius: 6px;
        text-align: left;
        font-weight: 600;
        font-size: 16px;
        line-height: 40px;
    }

    .search svg {
        width: 50px;
        height: 29px;
        border-left: 1px solid #000;
        cursor: pointer;
        margin-left: -55px;
        margin-top: -2px;
        z-index: 1000;
    }
}
</style>